<script>


	import {seachData,liuFen,queryCarousels} from '../../api/info';
	export default {
		data() {
			return {
				formInline:{
					oilName:'',
					oilPlace:'',
					oilArea:'',
					time:[],
					oiloilSulphur:[0,5],
					oilAcidity:[0,5],
					oilApi:[16,64],
					dateStart:'',
					dateEnd:'',
					oilAcidityMin:0,
					oilAcidityMax:0,
					oilApiMin:0,
					oilApiMax:0,
					oilSulphurMin:0,
					oilSulphurMax:0
				},
				imgUrlBanner:'',
				rules:{},
				total:0,    //总共条数
				pageNum:1,   // 当前页
				pageSize:10,  // 一页多少条
				listData:[],
				num:1,
				total1:0,
//				pageNum:1,
//				pageSize:10,
				infoArr:[],
				oli:''
			}
		},
		methods: {
			init(objData){    //初始化
				seachData(objData).then((data)=>{
					if(data.status == 9999) {
						window.location.href=configURL.sys+"/login.htm";
						//跳登入页面
						return false;
					}
					this.listData=data.datas;
					this.total=data.total;
				})
			},
			onSubmit(){  // 查询
				var data={};
				for(var name in this.formInline){
					if(this.formInline[name]!='' && name!='time')
					{
						if( name=="oilAcidity"){
							this.formInline.oilAcidityMin = this.formInline[name][0],
							this.formInline.oilAcidityMax= this.formInline[name][1]
						} else if(name=="oiloilSulphur") {
							this.formInline.oilSulphurMin = this.formInline[name][0],
							this.formInline.oilSulphurMax= this.formInline[name][1]
						} else if(name=="oilApi") {
							this.formInline.oilApiMin = this.formInline[name][0],
							this.formInline.oilApiMax= this.formInline[name][1]
						} else if(name!='oiloilSulphur' && name!='oilAcidity' && name!='oilApi'){
							data[name]=this.formInline[name];
						}
						
					}	
				}
				if(this.formInline.time){
					data.dateStart = this.formInline.time[0]
					data.dateEnd = this.formInline.time[1]
					this.formInline.dateStart = this.formInline.time[0]
					this.formInline.dateEnd = this.formInline.time[1]
				}else{
					data.dateStart = ''
					data.dateEnd = ''
					this.formInline.dateStart = ''
					this.formInline.dateEnd = ''
				}
				
//				console.log(data);
				// return false;
				
//				this.init(data);
				seachData(data).then((data)=>{
					this.listData=data.datas;
					this.total=data.total;
				})
			},
			getSTime(val) {   //时间转换
		   		Date.prototype.format = function(fmt) { 
				     var o = { 
				        "M+" : this.getMonth()+1,                 //月份 
				        "d+" : this.getDate(),                    //日 
				        "h+" : this.getHours(),                   //小时 
				        "m+" : this.getMinutes(),                 //分 
				        "s+" : this.getSeconds(),                 //秒 
				        "q+" : Math.floor((this.getMonth()+3)/3), //季度 
				        "S"  : this.getMilliseconds()             //毫秒 
				    }; 
				    if(/(y+)/.test(fmt)) {
				            fmt=fmt.replace(RegExp.$1, (this.getFullYear()+"").substr(4 - RegExp.$1.length)); 
				    }
				    for(var k in o) {
				        if(new RegExp("("+ k +")").test(fmt)){
				             fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));
				         }
				     }
				    return fmt; 
				}   
				
				this.formInline.dateStart = new Date(this.formInline.time[0]).format("yyyy-MM-dd");
				this.formInline.dateEnd = new Date(this.formInline.time[1]).format("yyyy-MM-dd");
				// console.log(this.formInline.dateStart);
         },
         pageFn(sty){    // 分页
         	var pageObj={'pageNum':sty,
						 'dateStart':this.formInline.dateStart,
						 'dateEnd':this.formInline.dateEnd,
						 'oilName':this.formInline.oilName,
						 'oilPlace':this.formInline.oilPlace,
						 'oilArea':this.formInline.oilArea,
						 'oilAcidityMin':this.formInline.oilAcidity[0],
						 'oilAcidityMax':this.formInline.oilAcidity[1],
						 'oilSulphurMin':this.formInline.oiloilSulphur[0],
						 'oilSulphurMax':this.formInline.oiloilSulphur[1],
						 'oilApiMin':this.formInline.oilApi[0],
						 'oilApiMax':this.formInline.oilApi[1]}
         	this.init(pageObj);
		 },
		 resetForm(formName) {   // 重置
			this.formInline.oilName = '';
			this.formInline.oilPlace = '';
			this.formInline.oilArea='';
			this.formInline.time=[];
			this.formInline.oiloilSulphur = [0,5];
			this.formInline.oilAcidity=[0,5];
			this.formInline.oilApi=[16,64];
			this.formInline.dateStart='';
			this.formInline.dateEnd='';
      },
      tabOne(){
      	this.num =1
      },
      tabTwo(){
      	this.num =2
      },
      liufenFn(){
				var param ={
					pageNum:this.pageNum,
					pageSize:this.pageSize,
				}
				liuFen(param).then(res =>{
					this.total1 = res.total
					this.infoArr = res.datas
				})
			},
			searchFn(){
				var params={
					pageNum:this.pageNum,
					pageSize:this.pageSize,
					crudeNameE:this.oli
				}
				liuFen(params).then(res =>{
					this.total1 = res.total
					this.infoArr = res.datas
				})
			},
			searData(){
				var params={
					pageNum:this.pageNum,
					pageSize:this.pageSize,
					crudeNameE:this.oli
				}
				liuFen(params).then(res =>{
					this.total1 = res.total
					this.infoArr = res.datas
				})
			},
			pageFn1(sty){
				this.pageNum = sty
				var param = {
					pageNum:sty,
					pageSize:this.pageSize,
					crudeNameE:this.oli
				}
				liuFen(param).then(res =>{
					this.total1 = res.total
					this.infoArr = res.datas
				})
			},
			 getBanner(){
				queryCarousels({typeCode:"WEB",pageCode:"zx_index_barner",pageNum:1,pageSize:10}).then((res)=>{
					
					this.imgUrlBanner = res.datas[0].imageUrl
					
				});
			},
			show2(){
				var params={
					pageNum:this.pageNum,
					pageSize:this.pageSize,
					crudeNameE:this.oli
				}
				liuFen(params).then(res =>{
					this.total1 = res.total
					this.infoArr = res.datas
				})
			}
          
		},
		mounted(){
			var urlStr=window.location.search.substring(window.location.search.indexOf('?')+1);
			var arrZhi=urlStr.split('&');
			var newObj={}
			
			for(var i=0;i<arrZhi.length;i++){
				var smallArr=arrZhi[i].split('=');
				newObj[smallArr[0]]=smallArr[1];
			}

          	this.init(newObj);
          	this.liufenFn()
          	this.getBanner()
        }
	}

</script>
